.scroll-top {
  position: fixed;
  bottom: 0;
  right: 0;
  cursor: pointer;
  color: #fff; }
  .scroll-top .arrow {
    padding: 0.5rem 1rem;
    text-align: center; }
    .scroll-top .arrow .to-top {
      text-align: center;
      font-size: 0.5rem;
      padding: 0.25rem; }
  .scroll-top .hide {
    display: none; }



